import React from 'react';
import logo from './../images/Vector.svg'; // Путь к изображению внутри сборки
import { Link, Route, Switch, useLocation, NavLink} from 'react-router-dom';

function Header({email, onSignOut}) {
  let location = useLocation();
  return (
    <header className="header root__section">

      <div className="logo__text">MESTO</div>
      {location.pathname === "/sign-up" && (
                <NavLink to="/sign-in" className="header__text">
                    Login
                </NavLink>
            )}
      {location.pathname === "/sign-in" && (
                <NavLink to="/sign-up" className="header__info">
                    Register
                </NavLink>
            )}
      {location.pathname === "/" && (
         <div className="header__elements">
         <div className="header__text">{email}</div>
         <div className="header__signout" onClick={onSignOut}>Logout</div>
         </div>
            )}      

    </header>
  );
}

export default Header;